<template>
  <div style="margin: 5px;">
          <el-table
            v-loading="loading"
            :data="tableData"
            stripe
            border
            highlight-current-row
            style="width: 100%; height: 100%"
            size="mini"
            :height="tableHeight"
            :header-cell-style="{'color':'#000', 'background': '#FAFAFA', 'text-align':'center'}">
            <el-table-column
              show-overflow-tooltip
              prop="FIELDNAME"
              label="字段名"
              width="180">
            </el-table-column>
            <el-table-column
              show-overflow-tooltip
              prop="DISSTR"
              label="中文名称"
              width="180">
            </el-table-column>
            <el-table-column
              label="类型">
              <template slot-scope="scope">
                 <el-select size="mini" v-model="scope.row.FIELDTYPE" @change="fieldTypeChange(scope.row)">
                  <el-option value="C" label="字符型">字符型</el-option>
                  <el-option value="I" label="数值型">数值型</el-option>
                  <el-option value="D" label="日期型">日期型</el-option>
                </el-select>
              </template>
            </el-table-column>
            <el-table-column
              show-overflow-tooltip
              label="长度"
              prop="FIELDSIZE"
              >
              <template slot-scope="scope">
                <el-input :value="scope.row.FIELDSIZE" size="mini" placeholder="字段长度"/>
              </template>
            </el-table-column>
            <el-table-column
              fixed="right"
              label="操作"
              width="100">
              <template slot-scope="scope">
                <el-button @click="handleClick(scope.row)" type="text" size="small">查看</el-button>
                <el-button type="text" size="small">编辑</el-button>
              </template>
            </el-table-column>
          </el-table>
  </div>
</template>
<script>
export default {
  name: 'StructSetup',
  data () {
    return {
      tableHeight: window.innerHeight - 70,
      loading: false,
      tableData: [],
      classId: '',
      classType: ''
    }
  },
  mounted: function () {
    this.$nextTick(function () {
      this.fetchData()
    })
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    windowResize () {
      this.tableHeight = window.innerHeight - 70
    },
    fieldTypeChange: function (row) {
      console.log(row.FIELDNAME, row.FIELDTYPE)
    },
    getFieldType: function (fieldType) {
      switch (fieldType) {
        case 'C':
        case 'V':
          return '字符型'
        case 'I':
          return '数值型'
        case 'D':
          return '日期型'
        default:
          return fieldType
      }
    },
    fetchData () {
      let that = this
      this.loading = true
      this.classId = this.$route.params.classId
      this.classType = this.$route.params.classType
      console.log(this.classId)
      this.axios.get(this.api + this.easyStarAPI.GetDocFrame, {
        params: {
          classId: this.classId,
          classType: this.classType
        }
      }).then(function (res) {
        that.loading = false
        console.log(res.data.cols)
        that.tableData = res.data.cols
      }).catch(function () {
        that.loading = false
      })
    },
    handleClick (row) {
      console.log(row)
    }
  }
}
</script>
<style>
.clearfix:before,
  .clearfix:after {
    height: 0px;
    padding: 0px;
  }
.card-header {
  color: #000;
  font-weight: 700;
}
  .clearfix:after {
    clear: both
  }
.el-row {
  margin-bottom: 10px;
}
.el-table--striped .el-table__body tr.el-table__row--striped.current-row td, .el-table__body tr.current-row > td {
  background: rgb(68, 208, 233);
  font-weight: 700;
  color: #000;
}
</style>



